import {Component, OnInit} from '@angular/core';
import {GlobalConfig, deviceLstColumnDefs, eventLstColumnDefs, extentTargetColumnDefs} from './global.component.config';
import {GlobalService, AuthenticationService, LoaderService} from '../../services/index';
import {GridOptions} from 'ag-grid/main';

// TODO remove after integrating with service
import {_tmpMultiAreaChartDatafn, _tmpPieChartDatafn, _tmpMultiBarChartDatafn} from '../../models/index';

@Component({
  selector: 'global-component',
  templateUrl: 'global.components.html',
  styleUrls: ['global.components.scss'],
  providers: [GlobalService],
})
export class GLOBALComponent {
  private deviceGridOptions: GridOptions;
  private filteredData = [];
  private extendGridOptions: GridOptions;
  private extentTargetGridOptions: GridOptions;

  private _tmpPieChartData = _tmpPieChartDatafn();
  private _tmpAreaChartData = _tmpMultiAreaChartDatafn();
  private _tmpBarChart = _tmpMultiBarChartDatafn();
  private _volumeLst = [];
  private _volumeItemSummary = {};

  constructor(private auth: AuthenticationService, private service: GlobalService, private loaderService: LoaderService) {
  }

  ngOnInit() {
    this.deviceGridOptions = <GridOptions>{
      columnDefs: deviceLstColumnDefs,
      rowData: null,
      enableSorting: true,
      enableColResize: true,
      rowSelection: 'single',
      rowHeight: 35,
      onGridReady: () => {
        this.deviceGridOptions.api.sizeColumnsToFit();
      }
    };
    this.extendGridOptions = <GridOptions>{
      columnDefs: eventLstColumnDefs,
      rowData: null, enableRowSelection: true,
      rowHeight: 35,
      enableColResize: true,
      enableSorting: true,
      rowSelection: 'single',
      onGridReady: () => {
        this.extendGridOptions.api.sizeColumnsToFit();
      }
    };

    this.extentTargetGridOptions = <GridOptions>{
      columnDefs: extentTargetColumnDefs,
      rowData: null, enableRowSelection: true,
      rowHeight: 35,
      enableColResize: true,
      enableSorting: true,
      rowSelection: 'single',
      onGridReady: () => {
        this.extentTargetGridOptions.api.sizeColumnsToFit();
      }
    };

    this.loadDeviceLst();
  }


  loadDeviceInfo(event) {
    let data = event.data;

    var volumeCapacityObj = {
      "totalVolume":parseFloat(data.storage.vol.volAvail.split(' ')[0]),
      "occupied":parseFloat(data.storage.vol.volUsed.split(' ')[0])
    }
    this._volumeItemSummary['capacityGraphData'] = this.getPieChartData(volumeCapacityObj);
    this._volumeItemSummary['naa'] = data['naa'];
    this._volumeItemSummary['HostName'] = data['hostName'];
    this._volumeItemSummary['lunId'] = data['lunId'];
    this._volumeItemSummary['extentType'] = data['extentType'];
    this._volumeItemSummary['VolumeName'] = data.storage.vol['volName'];
    this._volumeItemSummary['volMountpoint'] = data.storage.vol['volMountpoint'];
    this._volumeItemSummary['volStatus'] = data.storage.vol['volStatus'];
    this._volumeItemSummary['extentPath'] = data['extentPath'];
    this._volumeItemSummary['type'] = data['type'];
  }


  loadDeviceLst() {
    this.loaderService.display(true);
    this.service.getDeviceLst(this.auth.getUserProfile(), GlobalConfig.deviceServiceProvider)
      .subscribe(
        data => {
           data.map(res => {
              if(res.type =='VMFS' ){
                  this.filteredData.push(res);
              }
           });
           this.deviceGridOptions.rowData=this.filteredData;
           this.extendGridOptions.rowData=data;
           this.extentTargetGridOptions.rowData=data;

           if(this.filteredData.length > 0) {
             this.loadDeviceInfo({data:this.filteredData[0]});
           }
           this.loaderService.display(false);
        },
        err => {
          this.loaderService.display(false);
          this.deviceGridOptions.rowData = [];
        }
      );
  }

  refreshAreaChart(val) {
    this._tmpAreaChartData = _tmpMultiAreaChartDatafn();
  }

  tabselectionChange(selectedTab) {
    if (selectedTab.active && selectedTab.title === "Extents") {
      this.extendGridOptions.api.sizeColumnsToFit();
    }
    else if (selectedTab.active && selectedTab.title === "Associated Targets") {
      this.extentTargetGridOptions.api.sizeColumnsToFit();
    }
  }

  getPieChartData(d) {
    if (d)
      return [
        {
          key: 'free',
          value: Math.floor(d.totalVolume - d.occupied)
        },
        {
          key: 'used',
          value: Math.floor(d.occupied)
        }
      ];
    else
      return [];
  }
}




