import {NgModule} from '@angular/core';
import {BrowserModule} from '@angular/platform-browser';
import { FormsModule } from '@angular/forms';
import {HttpModule} from '@angular/http';
import {VMWebSphereClientAppRoutes} from './app.routes';
import {AppComponent} from './app.component';
import {AppHeader} from './components/appHeader.component';

// used to create fake backend
import { fakeBackendProvider } from './helper/index';
import { MockBackend } from '@angular/http/testing';
import { BaseRequestOptions } from '@angular/http';
import { GLOBALModule } from './modules/global/global.module';
import { ClusterModule } from '../app/modules/cluster/cluster.module';
import { AppRoutingModule } from './app-routing.module';
import { DataCenterModule } from './modules/datacenter/datacenter.module';
import { SharedModule } from './modules/shared-module/shared.module';
import { DataStoreModule } from './modules/datastore/datastore.module';
import { HostModule } from './modules/host/host.module';
import { SummaryModule } from './modules/summary-portlet/summary.module';
import {MultiBarChartComponent} from './components/MultiBarChart/multiBarChart.component';
import {DataCenterWizardComponent} from './components/DataCenterWizard/datacenterwizard.component';
import {DataCenterWizardModule} from './components/DataCenterWizard/datacenterwizard.module';
import {ProgressIndicator} from './components/ProgressIndicator/progressindicator.component';
import {CommonModule} from '@angular/common';
import {LoaderService} from './services/index'


@NgModule({
  imports: [
    BrowserModule,
    FormsModule,
    CommonModule,
    HttpModule,
    SharedModule,
    DataCenterModule,
    ClusterModule,
    GLOBALModule,
    DataStoreModule,
    HostModule,
    SummaryModule,
    DataCenterWizardModule,
    AppRoutingModule
  ],
  declarations: [
    AppComponent, AppHeader, ...VMWebSphereClientAppRoutes.map((c) => {
      return c.component;
    })
  ],
  bootstrap: [AppComponent],
  providers: [
    // providers used to create fake backend
    // fakeBackendProvider,
    // MockBackend,
    BaseRequestOptions,
    LoaderService
  ],

  entryComponents: [
    ...VMWebSphereClientAppRoutes.map((c) => {
      return c.component;
    }),
    MultiBarChartComponent,
    DataCenterWizardComponent,
    ProgressIndicator
  ]
})
export class AppModule {
}



