import {Component, Output, EventEmitter, OnInit} from '@angular/core';
import {GridOptions} from 'ag-grid/main';


@Component({
  selector: 'datacenterWizard-component',
  templateUrl: './datacenterwizard.component.html',
  styleUrls: ['./datacenterwizard.component.scss'],
  providers:[]
})
export class DataCenterWizardComponent implements OnInit {

  @Output() navToWizard: EventEmitter<any> = new EventEmitter<any>();
  @Output() previousStep:EventEmitter<any> = new EventEmitter<any>();
  @Output() nextWizStep:EventEmitter<any> = new EventEmitter<any>();
  @Output() closeDataStoreWizard:EventEmitter<boolean> = new EventEmitter<boolean>();

  //The data has to be moved to configuration

  private wizardConfig= {
    wizard_path: [{'path': 'dataStore_details','wizard':'DataStore Details','step':1},
      {'path': 'host_selection','wizard':'Host Selection','step':2},
      {'path': 'volume_configuration','wizard':'Volume Configuration','step':3},
      {'path': 'volume_group_configuration','wizard':'Volume Group Configuration','step':4},
      {'path': 'summary','wizard':'Summary','step':5}],
    dataStoreDetails: {
      dataStoreName: '',
      memoryUnitsSize: [200,300,400,500,600,700],
      selectedMemorySize: '',
      selectedMemoryUnit: '',
      memoryUnityType: ['TB','GB','MB'],
      vmfsVersion: ['VMFS5','VMFS4','VMFS3']
    },
    selectedPathId:1,
    headerLbl:"",
    hostSelection: {
      showCheckBox: true,
      dataLst:[{
        label: 'Vc6 EXSi 6.5 -a DataCenter',
        isExpanded: true,
        selected: false,
        subs: [
          {
            label: 'Host Cluster',
            isExpanded:true,
            selected: false,
            subs: [
              {
                label: '172.30.37.58',
                isExpanded: false,
                selected: false
              },
              {
                label: '172.30.37.118',
                isExpanded: false,
                selected: false
              }
            ]
          }
      ]
    }]
    },
    volumeConfig: {
      isNewVolumeSelected: true,
      isExistingVolumeSelected: false,
      volumeName: '',
      volumeGridOptions: <GridOptions>{
        columnDefs: [
          {headerName: "Name", field: "volumeName"},
          {headerName: "IQN", field: "iqn"},
          {headerName: "Capacity", field: "capacity"},
          {headerName: "Dedup", field: "dedup"}
        ],
        rowData: null, enableRowSelection: true,
        rowHeight: 35,
        enableColResize: true,
        enableSorting: true,
        rowSelection: 'single',
        onGridReady: () => {
          this.wizardConfig.volumeConfig.volumeGridOptions.api.sizeColumnsToFit();
        }
      }
    },
    volumeGroupConfig: {
      isNewVolumeSelected: false,
      isExistingVolumeSelected: true,
      volumeGroup:''
    }
  };

  constructor(){
  }

  ngOnInit(){
    this.navigateToWizard(0);
  }

  navigateToWizard(step:any){
    this.wizardConfig.selectedPathId=step;
    this.wizardConfig.headerLbl=this.wizardConfig.wizard_path[step].wizard;
  }

  nextStep(){
    if(this.wizardConfig.selectedPathId === 4 ? this.wizardConfig.selectedPathId = 0 : this.wizardConfig.selectedPathId++)
        this.navigateToWizard(this.wizardConfig.selectedPathId)
  }

  prevStep(){
    if(this.wizardConfig.selectedPathId === 0 ? this.wizardConfig.selectedPathId = 4 : this.wizardConfig.selectedPathId--)
      this.navigateToWizard(this.wizardConfig.selectedPathId)
  }

  closeWizard(){
    this.closeDataStoreWizard.emit(false);
  }

  volumeTypeToggleClick(name) {
    if(name === "newVolumeConfig") {
      this.wizardConfig.volumeConfig.isNewVolumeSelected = true;
      this.wizardConfig.volumeConfig.isExistingVolumeSelected = false;
    }
    else {
      this.wizardConfig.volumeConfig.isExistingVolumeSelected = true;
      this.wizardConfig.volumeConfig.isNewVolumeSelected = false;
    }
  }
}



